<script>
import { applicationService } from "@/services";

export default {
  data() {
    return {
      applications: []
    };
  },
  created() {
    this.getAllApplications();
  },
  methods: {
    getAllApplications() {
      applicationService.getAll().then(data => {
        this.applications = data.data;
      });
    }
  }
};
</script>
<template>
  <div class="page-wrapper">
    <div class="panel">
      <div class="panel-heading">
        <span class="title">Selecione uma aplicação para continuar</span>
      </div>
      <div class="panel-body">
        <ul class="listing">
          <li class="btn-link" v-for="app in applications" :key="app.id">
            <a :href="'/?auth_application_id='+app.id">{{app.id}} - {{app.name}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.page-wrapper {
  padding: 15px 30px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;

  .panel {
    width: 500px;
    font-family: roboto;

    .panel-heading {
      .title {
        font-weight: 500;
        font-size: 17px;
      }
    }

    .panel-body {
      ul.listing {
        display: flex;
        flex-direction: column;

        & > li {
          padding: 10px 10px;
          font-weight: 500;
          border-bottom: 1px solid #eee;
        }
      }
    }
  }
}
</style>


